import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DatosService } from '../../servicios/datos.service';
import { ActionSheetController } from '@ionic/angular';
import { AuthService } from '../../servicios/auth.service';



@Component({
  selector: 'app-servicios',
  templateUrl: './servicios.page.html',
  styleUrls: ['./servicios.page.scss'],
})
export class ServiciosPage implements OnInit {


  id:string;
  room:any;
  datosFire :any [];
  idLocal:string;
  arrayTest:any[];

  constructor(private activeRouter: ActivatedRoute, private dataService:DatosService, private actionSheetController: ActionSheetController, private auth: AuthService) { }

  ngOnInit() {
    this.idLocal = '';
    this.id = this.activeRouter.snapshot.paramMap.get('id');
    this.dataService.getUsers().subscribe( datos => {
      this.datosFire = datos;
      this.leerData(this.id);
    });
    
  }


  leerData(id:string) {

    for (let index = 0; index < this.datosFire.length; index++) {
      const element  = this.datosFire[index];
      if(id == element.uid){
        if(element.servicio != null){
          this.arrayTest = element.servicio;
        }else{
          this.arrayTest = null;
        }
      }  
    }
    
  }


  onlogout(){
    this.auth.logout();
  }



  async presentActionSheet() {
    const actionSheet = await this.actionSheetController.create({
      header: 'Opciones',
      buttons: [{
        text: 'Salir',
        role: 'destructive',
        icon: 'log-out',
        handler: () => {
          this.onlogout();
        }
      }]
    });
    await actionSheet.present();
  }


}
